<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>ADD NEW COCATEGORY
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>COID*</label>
                        {{coid}}
                        <select
                          v-model="coid"
                          class="form-control form-control-sm"
                          :disabled="disable"
                        >
                          <option
                            :value="subcategory.coid"
                            class="font-weight-bold"
                            v-for="subcategory in subcategories"
                            :key="subcategory.coid"
                          >{{subcategory.name}} - {{subcategory.coid}}</option>
                        </select>
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[2]"
                        >{{errors[2]}}</span>
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Name</label>
                        <input
                          type="text"
                          v-model="name"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[3]"
                        >{{errors[3]}}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Item ID</label>
                        <input
                          type="text"
                          v-model="item_id"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable||edit"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[4]"
                        >{{errors[4]}}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Price Band Value</label>
                        <input
                          type="number"
                          v-model.number="priceBandValue"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click.prevent="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <router-link to="/inventory" class="btn btn-sm btn-secondary">
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </router-link>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[1]">
                      <div class="col text-center text-danger">{{errors[1]}}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5 card shadow p-0 ml-2">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-list pl-1 pr-3"></i>CO CATEGORIES
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <table>
                    <thead>
                      <tr style="display: block; width: 97%;">
                        <th>Item Id</th>
                        <th>Co Id</th>
                        <th class="pl-2">Name</th>
                        <th class="pl-2">Actions</th>
                      </tr>
                    </thead>
                    <tbody
                      style="display: block; height: 75vh; max-height: 75vh; overflow-y: auto;"
                    >
                      <tr v-for="(cocategory,index) in cocategories" :key="cocategory.item_id">
                        <td>{{cocategory.item_id}}</td>
                        <td>{{cocategory.coid}}</td>
                        <td class="pl-2">{{cocategory.name}}</td>
                        <td class="pl-2">
                          <a
                            style="color:#424242;"
                            href="#"
                            title="delete this item"
                            @click.prevent="deleteCo(index)"
                          >
                            <i class="fa fa-trash"></i>
                          </a>
                          <a
                            style="color:#424242;"
                            href="#"
                            title="delete this item"
                            @click.prevent="editCoCategory(index)"
                          >
                            <i class="fa fa-pencil-alt"></i>
                          </a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-1"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import Vue from "vue";
import db from "../firebaseInit";
import firebase from "firebase";
import Loader from "../Loader";

import { firestorage } from "../firebaseInit";

export default {
  name: "cocategory",
  components: {
    Loader
  },
  data() {
    return {
      domain: "https://doorstepapi.000webhostapp.com/api/",

      coid: null,
      item_id: null,
      name: null,
      priceBandValue: 10,

      subcategories: [],
      cocategories: [],
      errors: [],
      edit: false,
      message: null,
      disable: false,
      disableMessage: null,
      success: false
    };
  },

  created() {
    this.fetchSubCategories();
    this.fetchCoCategories();
  },

  methods: {
    add() {
      if (this.validate()) {
        this.disable = true;
        let url = this.domain + "cocat";

        if (this.edit == false) {
          db.collection("co category")
            .add({
              coid: this.coid,
              item_id: this.item_id,
              name: this.name,
              priceBandValue: this.priceBandValue,
              uploadedAt: new Date(),
              updatedAt: new Date()
            })
            .then(res => {
              axios
                .post(
                  url,
                  {
                    coid: this.coid,
                    item_id: this.item_id,
                    name: this.name,
                    priceBandValue: this.priceBandValue
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": "application/json",
                      "Access-Control-Allow-Origin": "*",
                      "X-Requested-With": "XMLHttpRequest",
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.coid = null;
                  this.item_id = null;
                  this.name = null;
                  this.priceBandValue = 10;

                  this.cocategories.unshift(res.data);

                  //// to show
                  this.message = "Data Upload Success";
                  this.success = true;
                  this.disable = false;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          url = this.domain + "cocat/" + this.item_id;
          db.collection("co category")
            .where("item_id", "==", this.item_id)
            .get()
            .then(snap => {
              return snap.docs[0].ref.update({
                coid: this.coid,
                item_id: this.item_id,
                name: this.name,
                priceBandValue: this.priceBandValue,
                updatedAt: new Date()
              });
            })
            .then(res => {
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    coid: this.coid,
                    name: this.name,
                    priceBandValue: this.priceBandValue
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": "application/json",
                      "Access-Control-Allow-Origin": "*",
                      "X-Requested-With": "XMLHttpRequest",
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.coid = null;
                  this.item_id = null;
                  this.name = null;
                  this.priceBandValue = 10;

                  this.cocategories.forEach((doc, index) => {
                    if (doc.item_id == res.data.item_id) {
                      Vue.set(this.cocategories, index, res.data);
                    }
                  });

                  this.edit = false;

                  //// to show
                  this.message = "Data Upload Success";
                  this.success = true;
                  this.disable = false;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  // delete in firebase
                  db.collection("co category")
                    .where("item_id", "==", this.item_id)
                    .get()
                    .then(res => {
                      res.forEach(doc => {
                        doc.ref.delete();
                      });

                      /// to show
                      this.message = "Data Upload In MYSql Failed";
                      this.success = true;
                      this.disable = false;

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    });
                });
            })
            .catch(err => {
              console.log(err);
            });
        }
      }
    },

    deleteCo(index) {
      if (confirm("You are about to delete " + this.cocategories[index].name)) {
        db.collection("co category")
          .where("item_id", "==", this.cocategories[index].item_id)
          .get()
          .then(res => {
            res.forEach(doc => {
              doc.ref.delete();
            });
          })
          .then(rep => {
            let url = this.domain + "cocat/" + this.cocategories[index].item_id;
            axios
              .post(
                url,
                {
                  _method: "delete"
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": "application/json",
                    "Access-Control-Allow-Origin": "*",
                    "X-Requested-With": "XMLHttpRequest",
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                if (res.data) {
                  this.cocategories.splice(index, 1);
                }
              });
          });
      }
    },

    editCoCategory(index) {
      if (this.cocategories) {
        //it's editing not adding
        this.edit = true;
        this.disableId = true;
        //details
        this.item_id = this.cocategories[index].item_id;
        this.name = this.cocategories[index].name;
        this.coid = this.cocategories[index].coid;
        this.priceBandValue = this.cocategories[index].priceBandValue;
      }
    },

    fetchCoCategories() {
      let url = this.domain + "cat";
      axios
        .get(url, {
          params: {
            cocategories: "budbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            doc.subcategories.forEach(subcat => {
              subcat.cocategories.forEach(cocat => {
                this.cocategories.push(cocat);
              });
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    fetchSubCategories() {
      let url = this.domain + "subcat";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            this.subcategories.push(doc);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    goBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push("/inventory");
    },

    validate() {
      this.errors = [];
      if (this.item_id && this.name && this.coid) {
        return true;
      } else {
        if (!this.item_id) {
          Vue.set(this.errors, 4, "Fill valid Item Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 3, "Fill Name");
        }
        if (!this.coid) {
          Vue.set(this.errors, 2, "Choose Subcategory");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>





