<template>
  <div>
    <Dashboard :products="items" :loading="loading" v-if="items"></Dashboard>
  </div>
</template>

<script>
import axios from "axios";
import db from "../firebaseInit";
import Dashboard from "./DashBoard.vue";
import { bus } from "../../main";
import { domain } from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";

export default {
  name: "searchItem",
  data() {
    return {
      items: [],
      loading: false
    };
  },

  components: {
    Dashboard
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  created() {
    bus.$on("searchItem", data => {
      this.searchItem(data["searched"]);
    });
  },

  methods: {
    searchItem(searched) {
      this.loading = true;
      this.items = [];

      // loading content
      let url = domain + "search";

      axios
        .get(url, {
          params: {
            data: searched,
            table: "items",
            offset: "0"
          },
          headers: {
            from: "webapp"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            {
              var item = new Array();
              let active = 0;
              // image name
              let imageName = null;
              if (doc.image) {
                let start = doc.image.indexOf("items%2F") + 8;
                let end = doc.image.indexOf("?");
                imageName = doc.image.substring(start, end);
                imageName = this.truncate(imageName);
              }
              if (doc.active == 1) {
                active = 1;
              } else {
                active = 0;
              }
              item = {
                productId: doc.productId,
                itemId: doc.itemId,
                name: doc.name,
                cat: doc.allcat,
                stock: doc.stock,
                unit: doc.unit,
                mrp: doc.MRP,
                currentPrice: doc.currentPrice,
                discount: doc.discount,
                image: doc.image,
                imageName: imageName,
                showImage: 0,
                qty: doc.qty,
                active: active,
                uploadedBy: doc.uploadedBy,
                uploadedAt: doc.uploadedAt
                // uploadedByName: doc.user.name

                // cocatName: doc.cocategory.name,
                // cocatId: doc.cocategory.coid,
                // subcatName: doc.cocategory.subcategory.name,
                // subcatId: doc.cocategory.subcategory.subcatid,
                // catName: doc.cocategory.subcategory.category.name
              };

              this.items.push(item);

              // getting co cat, subcat and cat
              let Courl = domain + "cocat";
              axios
                .get(Courl, {
                  params: {
                    item_id: item.itemId
                  }
                })
                .then(crres => {
                  crres.data.forEach(doc => {
                    item["cocatName"] = doc.name;
                    item["cocatId"] = doc.coid;

                    let Suburl = domain + "subcat";
                    axios
                      .get(Suburl, {
                        params: {
                          coid: doc.coid
                        }
                      })
                      .then(subres => {
                        subres.data.forEach(sub => {
                          item["subcatName"] = sub.name;
                          item["subcatId"] = sub.subcatid;

                          let Caturl = domain + "cat";
                          axios
                            .get(Caturl, {
                              params: {
                                subcatcode: sub.subcatid
                              }
                            })
                            .then(catres => {
                              catres.data.forEach(doc => {
                                item["catName"] = doc.name;
                              });
                            });
                        });
                      });
                  });
                });
            }
          });

          store.dispatch("patchinventoryPageCount", res.data.last_page);
          store.dispatch("patchinventoryPageNumber", res.data.current_page);
          store.dispatch("patchinventoryTotalItems", res.data.total);
        })
        .catch(err => {
          console.log(err);
        });

      this.loading = false;
    },

    truncate(value) {
      const length = 10;
      return value.length <= length
        ? value
        : value.substring(0, length) + "...";
    }
  },

  sockets: {
    message(data) {
      console.log(data);
    }
  }
};
</script>




